import {getLyric} from 'api/song'
import {ERR_OK} from 'api/config'
import {Base64} from 'js-base64'

export default class Song {
  constructor({id, mid, singer, name, album ,albummid ,duration, image,local_image, url,kugou_url="",lyric,play_mode="",local_url="",paly_mode,update_time,create_time}) {
    this.id = id
    this.mid = mid
    this.singer = singer
    this.name = name
    this.album = album
    this.albummid = albummid
    this.duration = duration
    this.image = image
    this.url = url
    this.kugou_url = kugou_url;
    this.play_mode= play_mode;
    this.local_url = local_url;
    this.lyric = lyric;
    this.paly_mode = paly_mode;
    this.update_time = update_time;
    this.create_time = create_time;
    this.local_image = local_image;
  }

  getLyric() {
    return new Promise((resolve, reject) => {
      if(this.lyric){
          this.lyric = decodeURIComponent(this.lyric);
          resolve(this.lyric)
      }else{
          getLyric(this.mid).then((res) => {
              if (res.retcode === ERR_OK) {
                  this.lyric = Base64.decode(res.lyric)
                  resolve(this.lyric)
              } else {
                  reject('no lyric')
              }
          })
      }

    })
  }
}

export function createSong(musicData) {
  return new Song({
    id: musicData.songid || musicData.id,
    mid: musicData.songmid || musicData.mid,
    singer: filterSinger(musicData.singer),
    name: musicData.songname || musicData.name,
    album: musicData.albumname||musicData.album.name,
    albummid: musicData.albummid || musicData.album.mid,
    duration: musicData.interval,
    image: `https://y.gtimg.cn/music/photo_new/T002R300x300M000${musicData.albummid||musicData.album.mid}.jpg?max_age=2592000`,
    url:""
  })  
}

export function filterSinger(singer) {
  let ret = []
  if (!singer) {
    return ''
  }
  singer.forEach((s) => {
    ret.push(s.name)
  })
  return ret.join('/')
}




